.tab-content {
  display: flex;
  flex-wrap: wrap;
  position: relative;
  min-width: 1160px;
  z-index: 1;

  @include tablet {
    flex-direction: column;
    min-width: auto;
  }

  &::before {
    content: "";
    position: absolute;
    right: -286px;
    bottom: -269px;
    width: 470px;
    height: 494px;
    background-image: url("../img/bg-plates-desktop@1x.png");
    background-size: 100%;
    z-index: -1;
    transform: translateX(0);
    overflow: hidden;

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/bg-plates-desktop@2x.png");
    }

    @include tablet {
      right: -239px;
      bottom: -223px;
      width: 423px;
      height: 444px;
      background-image: url("../img/bg-plates-tablet@1x.png");

      @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
        background-image: url("../img/bg-plates-tablet@2x.png");
      }
    }

    @include mobile {
      display: none;
    }
  }

  &.hidden {
    display: none;
  }

  li:not(:nth-child(3n)) {
    margin-right: 40px;

    @include tablet {
      margin-right: 0;
      margin-bottom: 40px;
    }

    @include mobile {
      margin-bottom: 35px;
    }
  }

  li:not(:nth-child(-n + 3)) {
    margin-top: 25px;
  }
}

.tab-content__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 56px 69px 67px;
  width: 360px;
  border: 7px solid transparent;
  background-color: $white;

  @include tablet {
    padding: 62px 111px 56px;
    width: 442px;
    height: 448px;
  }

  @include mobile {
    padding: 39px 35px;
    width: 290px;
    height: 384px;
  }

  &:hover {
    border: 7px solid;
    border-image: linear-gradient(143.02deg, #d10a42 0.85%, #00359f 100%);
    border-image-slice: 1;

    .tab-content__link {
      width: 207px;
      height: 64px;
      border: none;
      background-image: linear-gradient(111.72deg, #ed0233 17.6%, #003eb7 95.31%);
      color: $white;
      transition: none;
    }
  }

  &:focus-within {
    border: 7px solid;
    border-image: linear-gradient(143.02deg, #d10a42 0.85%, #00359f 100%);
    border-image-slice: 1;

    .tab-content__link {
      width: 207px;
      height: 64px;
      border: none;
      background-image: linear-gradient(111.72deg, #ed0233 17.6%, #003eb7 95.31%);
      color: $white;
      transition: none;
    }
  }

  h3 {
    margin-bottom: 10px;
    max-width: 180px;
    color: $text-color;
    font-family: $roboto;
    font-size: 26px;
    font-weight: 700;
    line-height: 29px;
    text-align: center;

    @include mobile {
      font-size: 22px;
      line-height: 24px;
    }
  }

  p {
    &:first-of-type {
      margin-bottom: 65px;
      color: $grey;
      font-family: $roboto;
      font-size: 16px;
      line-height: 24px;

      @include mobile {
        margin-bottom: 52px;
      }
    }

    &:last-of-type {
      position: relative;
      margin-bottom: 34px;
      color: $blueDark;
      font-family: $roboto;
      font-size: 60px;
      font-weight: 700;
      line-height: 90px;
      z-index: 1;

      @include tablet {
        margin-bottom: 38px;
      }

      @include mobile {
        margin-bottom: 30px;
        font-size: 48px;
        line-height: 72px;
      }

      &::before {
        content: attr(data-value);
        position: absolute;
        bottom: -5px;
        left: 50%;
        transform: translateX(-50%);
        color: #e8e8e8;
        font-family: $roboto;
        font-size: 100px;
        font-weight: 700;
        line-height: 150px;
        opacity: 0.5;
        z-index: -1;

        @include mobile {
          font-size: 80px;
          line-height: 120px;
        }
      }

      span {
        color: $text-color;
        font-weight: 700;
      }
    }
  }

  .tab-content__link {
    width: 207px;
    height: 66px;
    color: $text-color;
    border: 2px solid $text-color;
    font-weight: 700;
    letter-spacing: 0.7px;
    transition: none;
  }
}
