.contacts {
  width: 50%;
  border-top: 1px solid #74819c;

  @include tablet {
    width: 100%;
  }
}

.contacts__content {
  padding: 68px 0 64px 120px;
  max-width: 683px;

  @include tablet {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0 auto;
    padding: 69px 103px 64px;
    max-width: 1023px;
    min-width: 768px;
  }

  @include mobile {
    padding: 68px 15px 59px;
    max-width: 767px;
    min-width: 320px;
  }

  h2 {
    @include h2;
    margin-bottom: 43px;

    @include tablet {
      margin-bottom: 33px;
      padding-left: 30px;
      width: 562px;
    }

    @include mobile {
      margin-bottom: 36px;
      padding-left: 0;
      width: 290px;
    }
  }
}

.contacts__list {
  display: flex;
  flex-wrap: wrap;
  position: relative;

  @include tablet {
    padding-left: 25px;
    width: 562px;
  }

  @include mobile {
    padding-left: 0;
    width: 290px;

    display: flex;
  }

  &::before {
    content: "";
    position: absolute;
    left: calc(40% + 5px);
    top: 5px;
    width: 1px;
    height: 100%;
    background-color: #74819c;

    @include tablet {
      top: 8px;
      left: calc(50% + 15px);
    }

    @include mobile {
      display: none;
    }
  }

  li {
    width: 170px;
    color: $text-color;
    font-family: $roboto;

    @include tablet {
      width: 218px;
    }

    @include mobile {
      width: 290px;

      &:nth-child(1) {
        order: 1;
      }

      &:nth-child(2) {
        order: 3;
      }

      &:nth-child(3) {
        order: 2;
      }

      &:nth-child(4) {
        order: 4;
      }
    }

    &:nth-child(2n + 1) {
      margin-left: 5px;
      margin-right: 120px;

      @include tablet {
        margin-right: 97px;
      }

      @include mobile {
        margin-left: 0;
        margin-right: 0;
      }
    }

    &:nth-child(2n) {
      width: 165px;
    }

    &:not(:nth-child(-n + 2)) {
      margin-top: 19px;

      @include mobile {
        margin-top: 0;
      }
    }

    @include mobile {
      &:not(:last-child) {
        margin-bottom: 20px;
      }
    }
  }

  h3 {
    margin-bottom: 2px;
    font-size: 16px;
    font-weight: 700;
    line-height: 24px;

    @include tablet {
      margin-bottom: 6px;
      font-size: 20px;
      line-height: 30px;
    }
  }

  p {
    font-size: 14px;
    line-height: 21px;

    @include tablet {
      font-size: 18px;
      line-height: 27px;
    }
  }

  a {
    color: $text-color;
    text-decoration: none;

    &:hover,
    &:focus {
      opacity: 0.8;
    }

    &:active {
      opacity: 0.6;
    }
  }
}
