.advantages {
  background-color: #f8f9fc;
}

.advantages__content {
  flex-direction: column;
  align-items: center;
  padding-top: 120px;
  padding-bottom: 114px;

  @include tablet {
    padding-top: 102px;
    padding-bottom: 92px;
  }

  @include mobile {
    padding: 70px 0 27px;
  }

  h2 {
    @include h2;
    margin-bottom: 62px;

    @include tablet {
      margin-bottom: 42px;
    }

    @include mobile {
      margin-bottom: 44px;
    }
  }
}

.advantages__list {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  min-height: 260px;
  width: 100%;

  @media (max-width: 1280px) {
    justify-content: space-between;
  }

  @include tablet {
    justify-content: flex-start;
    min-height: 239px;
  }

  @include mobile {
    flex-direction: column;
    align-items: center;
  }
}
